import React from 'react';
import Layout from '../components/Layout/Layout';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import { HeaderContainer, HeaderImage } from "../components/Salas/Header1.js";
import { Typography, Box } from "@mui/material";
import { BannerContainer, BannerContent, BannerDescription, BannerImage } from "../components/Salas/Banners";
import can from "../images/can.jpeg";
import son from "../images/Sonora.jpeg";
import son2 from "../images/Sonora2.jpeg";
import son3 from "../images/Sonora3.jpeg";
import son4 from "../images/Sonora4.jpeg";
import son5 from "../images/Sonora5.jpeg";
import coco from "../images/cocospera2.jpg";
import bavi from "../images/Bavispe2.jpeg";
import bavi2 from "../images/Bavispe3.jpeg";
import bosque from "../images/bosque.jpeg";
import enci from "../images/Encinal.jpeg";
import sequi from "../images/Sequia.jpeg";
import bavi3 from "../images/Bavispe.jpeg";
import img1s1 from '../images/gallery/sala1/figura1_sala1.jpeg'
import img2s1 from '../images/gallery/sala1/figura2_sala1.jpeg'
import img3s1 from '../images/gallery/sala1/figura3_sala1.jpeg'

export default function Sala1() {
  return (

    <Layout>
      <HeaderContainer>
        <HeaderImage src={can} />
      </HeaderContainer>
      <br />
      
      <h2 style={{ textAlign: 'center', marginTop: '15px' }}>Primeros Habitantes</h2>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={son} width={50} />
        <BannerContent>
          <Typography variant="h4">Los Pueblos Ancestrales y el Contacto con los Espaoles.</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          La antigedad de las culturas del noroeste se remonta al Pleistoceno, hace quince mil aos hacia 200 d.C se desarrollaron pequeas aldeas agrcolas de casas-foso.
          En Sonora se han identificado cinco tradiciones arqueolgicas: Trincheras al norte, Paquim al oriente, Serrana al centro, la Costa Central y Huatabampo en el sur de Sonora
          y del norte de Sinaloa. La regin de Cananea est ubicada en los lmites entre Trincheras y Paquim.</BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={son2} />
        <BannerContent>
          <Typography variant="h4">Primeras Expediciones (rutas y caminos)</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          Los iniciales reconocimientos por parte de conquistadores espaoles se registraron a partir de 1527, con la travesa de lvar Nez Cabeza de Vaca desde el norte. 
          Un lustro despus una partida de espaoles procedentes de Culiacn llegaron hasta Cumuripa y Cerro Prieto con el propsito de capturar esclavos, en 1539 fray Marcos de Niza 
          recorri el territorio, al siguiente ao Francisco Vzquez de Coronado, en su camino al ro Gila, fund un efmero pueblo en territorio pata (Corazones). 
          En 1565 Francisco de Ibarra transit, como lo haban hecho los conquistadores que le precedieron, los antiguos caminos indgenas.</BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={son3} />
        <BannerContent>
          <Typography variant="h4">Grupos de Contacto</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          Las crnicas espaolas y la arqueologa consignan la existencia de una alta poblacin pata y registran que al momento de los primeros contactos vivan en aldeas, pueblos y rancheras, 
          habitando casas en la superficie (de adobe) y casas-foso: los asentamientos poblacionales guardaban cierta jerarqua y haba centros regionales con mayor influencia. La situacin encontrada 
          por los jesuitas fue diferente, esta nacin (y en general las que poblaban lo que se llam Provincia de Sonora) viva en pequeas rancheras dispersas debido a que su poblacin haba sido diezmada por las epidemias. 
          Se estima que a fines del siglo XVII haba una poblacin de 20,000 patas.</BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={son4} />
        <BannerContent>
          <Typography variant="h4">Inicio de la Evangelizacin</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          Con la evangelizacin jesuita en la opateria se empez a definir la frontera norte de Sonora, para 1670 los padres de la Compaa de Jess estaban bien asentados entre patas y pimas bajos, 
          pero no haban tenido xito con pimas altos y seris. El panorama cambi con la llegada del padre Eusebio Francisco Kino en 1687, quien fund las misiones de la pimeria alta, iniciando en 1687 con Nuestra Seora de los Dolores (al norte de Cucurpe, el pueblo pata ms septentrional en la vega del ro San Miguel): 
          fundo tambin Santa Mara Suamca, Los Santos ngeles de Guevavi y Santiago de Cocspera, pronto abandonadas debido a los asaltos apaches.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={son5} />
        <BannerContent>
          <Typography variant="h4">La Etapa Colonial, Pueblos de Misin</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          El propsito de las misiones jesuitas fue convertir los indgenas al cristianismo y transformarlos en sbditos del rey de Espaa. La misin y el presidio, instituciones complementarias, 
          sostuvieron e hicieron posible el asentamiento y dominio de los colonos civiles (conquista secular). En los pueblos de misin, los patas, como otras naciones indgenas de Sonora, 
          transformaron su organizacin social, poltica y militar, incorporaron nuevas formas productivas, modificaron su dieta y dieron nuevos sentidos a sus imaginarios sociales debido a la influencia de las creencias religiosas cristianas, 
          que combinaron con sus costumbres ancestrales originando nuevos ritos y ceremonias.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={coco} />
        <BannerContent>
          <Typography variant="h4">Presidios y Frontera</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          Los presidios fueron establecidos para afianzar la frontera ganada por los conquistadores espaoles hasta el siglo XVII. Tuvieron como objetivo guardar el orden entre los indios sujetos a pueblos de misin, 
          que expresaban sus inconformidades mediante frecuentes levantamientos armados, en ocasiones aliados a indgena no reducidos al sistema misional, como los seris. Asimismo, los presidios tuvieron como responsabilidad principal guarecer la frontera de las incursiones apaches, 
          presentes desde 1680.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerContent>
          <Typography variant="h4">Crisis del Sistema Misional, Reformas Borbnicas</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          Las Reformas Borbnicas impulsadas por la corona espaola en la segunda mitad del siglo XVIII se acompaaron de trascendentes cambios en el orden poltico y econmico, entre ellos la expulsin de los jesuitas en 1767 y su relevo por franciscanos, 
          numerosos pueblos de la opatera fueron secularizados, es decir, convertidos en parroquias o doctrinas: la poblacin indgena decreci y aument la constituida por colonos civiles (espaoles, criollos y castas o gente de razn) 
          a los que paulatinamente se transfiri la tierra.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={bavi} />
        <BannerContent>
          <Typography variant="h4">Islas del Cielo</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          Las montaas de esta regin se conocen como Islas del Cielo. Separadas unas de otras por un mar de pasto, 
          lejos del macizo principal de la Sierra Madre Occidental, que hace apenas 30 millones de aos dejo los grandes bloques de roca que forman las cimas y 
          han ido llenando los valles con sus desprendimientos de grava y arena.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={bavi2} />
        <BannerContent>
          <Typography variant="h4">Pastizal</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          En el mar de pasto que rodea las montaas de la regin se observan especies anuales, que brotan cada ao, y especies perennes que viven varios aos soportando las largas temporadas sin lluvia y heladas invernales. 
          A las especies nativas se encuentran los quelites (Amaranthus), el garbancillo (critalaria plumila), y varias especies de zacates conocidos como navajitas (gnero Bouteloua), 
          el zacate galleta (Hilaria belangeri) muy apreciado por el ganado que pasta en la zona.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={bosque} />
        <BannerContent>
          <Typography variant="h4">Bosques de Galera</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          Es en los pastizales donde surge uno de los fenmenos ms llamativos de la ecologa del desierto: Los bosques de galera, que con el follaje de los lamos (Populus fremontii), 
          sauces (Salix gooddingii y Salix bonplandiana) y en las zonas ms ridas o impactadas, incluso con los mezquites (Prosopis velutina) 
          que gana territorio gracias a un sistema de races ms profundas y mayor resistencia a la sequa.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={enci} />
        <BannerContent>
          <Typography variant="h4">Encinal</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          En los lomeros a partir de los 1,300 msnm dominan los encinos (gnero Quercus) y llegan a convivir con los pinos en la parte media de las montaas. 
          Las especies ms comunes en la zona son el encino blanco (Q, arizonical), encino bellotero (Q, emoryi) bellota de cochi (Q, oblongifolia) y 
          conviven con rboles ms comunes en los matorrales como l, tscate (juniperus coahuilensis) y el mezquite (Prosopis velutina), que logran reproducirse ms fcilmente, 
          ya que el ganado consume los brotes jvenes de los encinos.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerContent>
          <Typography variant="h4">Pinar</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          Encaramados en los riscos y mezclados con los encinos a la orilla de la carretera que lleva Cananea se ven los pinos (gnero Pinus) de diversas especies, Pionero (P, discolor), 
          chimonque (P, leiophila), pino real (P, engelmanii) y pino blanco (P, arizonica). Muy apreciados por la calidad de su madera, la extraccin forestal de pinos ha cambiado su distribucin y 
          la proporcin con los encinos en los estratos donde conviven.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={sequi} />
        <BannerContent>
          <Typography variant="h4">La Sequa y Los Troncos Secos</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          La sequa forma parte de los grandes ciclos climticos de esta regin y las especies se adaptan refugindose en las cimas donde la lluvia es ms abundante y 
          regresando cuando las condiciones son ms favorables. Cuando los rboles mueren, sus troncos son aprovechados por hongos e insectos que reintegran lentamente los nutrientes al suelo y 
          varias aves los buscan como refugio.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerContent>
          <Typography variant="h4">Los Incendios</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          En zonas ms altas los incendios regulan la convivencia de pinos y encinos, 
          que ganan espacio temporalmente al poder surgir ms rpido cuando finalmente llegan las lluvias y brotan las semillas y las races. 
          Los esfuerzos de reforestacin apresurada despus de los incendios, a veces con especies exticas, ponen en riesgo los equilibrios construidos por la naturaleza a travs de millones de aos.
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <BannerContainer style={{ marginTop: '15px', borderRadius: '15px' }}>
        <BannerImage src={bavi3} />
        <BannerContent>
          <Typography variant="h4">Islas nicas</Typography>
          <BannerDescription variant="h6" style={{ marginTop: '10px' }}>
          En esta zona se han identificado 1,234 especies de plantas, 208 especies de aves y 156 especies de mariposas. Esta gran diversidad de invertebrados y relaciones de plantas y animales que han evolucionado juntos al punto de ser comunidades nicas como los colibres y 
          los agaves se explica por la extraordinaria situacin de estas montaas en el lmite de dos reinos florsticos y dos reinos faunsticos a travs de tres zonas climticas(tropical, subtropical y templado).
          </BannerDescription>
        </BannerContent>
      </BannerContainer>

      <br />

      <h1 style={{ fontFamily: '"BlinkMacSystemFont"', textAlign: 'center', marginTop: '15px' }}>Galera</h1>
      <Box sx={{ marginTop: "15px" }}>
        <ImageList variant="masonry" cols={3} gap={8}>
          {itemData.map((item) => (
            <ImageListItem key={item.img}>
              <img
                src={`${item.img}?w=248&fit=crop&auto=format`}
                srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                alt={item.title}
                loading="lazy"
              />
            </ImageListItem>
          ))}
        </ImageList>
      </Box>
      <br />
    </Layout>
  );
}

const itemData = [
  {
    img: img3s1,

  },
  {
    img: img1s1,

  },
  {
    img: img2s1,

  },
];